
import { getCanUserTicketist } from '@/api/user.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import noData from '@/components/noData.vue'
export default {
name: 'loginRegister',
components: {
Tab,
TabItem,
noData,
XButton
},
data () {
return {
stroke: 'static/images/stroke.png',
strokestaus: 0,
isShow: false,
checkid: 'no',
canusenum: '',
notusenum: '',
tabnum: 0,

curid: '',
oldpreid: '',
curlistid:'',
canuselist: [],
notuselist: []
}
},











methods: {
chiose: function (strokeid) {
if (this.checkid === strokeid) {
this.checkid = 'no'
this.curlistid = 0
} else {
this.checkid = strokeid
this.curlistid = strokeid
}
console.log(this.checkid)
},
subRegister () {
this.$router.push({
name: 'confirmOrder',
query: {
preid: this.oldpreid,
tid: this.checkid
}
})
},
showitem (index) {
if (index === 0) {
this.tabnum = 0
} else if (index === 1) {
this.tabnum = 1
}
},
couponRuleMain: function () {
this.isShow = !this.isShow
},
myTicketist () {
const that = this
const postData = {
preid: that.oldpreid,
curlistid: that.curlistid
}
getCanUserTicketist(postData)
.then(res => {
console.log(res)
if(res.data.data===null){
this.$router.replace("/usercenter/myorder")
return
}
if (res.data.errorcode === 0) {
that.canuselist = res.data.data.canuselist
that.notuselist = res.data.data.notuselist
if (res.data.data.canuselist.length === 0) {
this.canusenum = '无'
} else {
this.canusenum = res.data.data.canuselist.length
}
if (res.data.data.notuselist.length === 0) {
this.notusenum = '无'
} else {
this.notusenum = res.data.data.notuselist.length
}
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})
}
},
created () {
this.oldpreid = this.$route.query.preid
this.curlistid = this.$route.query.tid
if(this.curlistid !==0 || this.curlistid !== null){
this.checkid = this.curlistid
}
this.myTicketist()
}
}



